body {
  box-sizing: border-box;
  font-optical-sizing: auto;
  font-variation-settings: "slnt" 0, "CRSV" .5, "ELSH" 0, "ELXP" 0;
  background-image: var(--image);
  scroll-behavior: smooth;
  background-position: center;
  background-size: cover;
  font-family: Tinos, system-ui;
  font-style: normal;
  font-weight: 400;
  overflow-x: hidden;
}

.theme-default {
  --bs-secondary: #d99f0d;
  --bs-primary: #060642;
  --bs-primary-light: #200242;
  --bs-secondary-light: #fcbd1d;
  --bs-effect: #75b6ff;
  --bs-effect-secondary: #060642;
  --bs-text: azure;
  --bs-text-secondary: #d99f0d;
  --image: url("graph-paper-blue.070fc663.png");
}

.theme-dark {
  --bs-secondary: #06682f;
  --bs-primary: #0d0d0d;
  --bs-primary-light: #20202083;
  --bs-secondary-light: #0adf63;
  --bs-effect: #0adf63;
  --bs-effect-secondary: #222;
  --bs-text: #babbbb;
  --bs-text-secondary: #058038;
  --image: url("graph-paper-dark.04781883.png");
}

.theme-light {
  --bs-secondary: #e4dcdc;
  --bs-primary: azure;
  --bs-primary-light: white;
  --bs-secondary-light: #d6c8c8;
  --bs-effect: red;
  --bs-effect-secondary: azure;
  --bs-text: #740b20;
  --bs-text-secondary: black;
  --image: url("graph-paper-white.f6f92a8f.png");
}

.btn {
  background-color: var(--bs-secondary);
  color: var(--bs-text);
}

#navbar {
  background-color: var(--bs-secondary);
}

.navbar-collapse {
  max-height: 50vh;
  overflow-y: auto;
}

h1, a, .card-title {
  color: var(--bs-text-secondary);
  font-weight: bold;
  text-decoration: none;
}

h2, h3 {
  color: var(--bs-text);
}

.nav-link, .navbar-toggler-text {
  color: var(--bs-text);
  text-decoration: none;
}

.nav-link:hover, .btn:hover {
  color: var(--bs-effect-secondary);
  text-decoration: none;
}

#loading:after {
  content: "";
  animation: 1.5s steps(5, end) infinite dots;
}

a:hover, a:focus {
  color: var(--bs-effect);
  text-decoration: none;
}

.card {
  will-change: transform;
  backface-visibility: hidden;
  background-color: var(--bs-primary);
  color: var(--bs-text);
  border-radius: 20px;
  transition: transform .3s ease-in-out;
  transform: scale(1);
  border: 2px solid var(--bs-secondary) !important;
}

.card .list-group-item {
  font-size: 15px;
}

.card:hover, .list-group-item:hover {
  background: linear-gradient(45deg, var(--bs-primary), var(--bs-primary-light));
  color: var(--bs-text);
}

.icon-link:hover {
  animation: .5s linear bounce;
}

.was-validated input:invalid {
  border: 3px solid #dc3545;
  box-shadow: 0 0 0 .25rem #dc354540;
}

.was-validated input:valid {
  border: 3px solid #28a745;
  box-shadow: 0 0 0 .25rem #28a74540;
}

.form-text, .toast-message {
  color: var(--bs-text);
}

.status {
  background-color: var(--bs-secondary);
  color: var(--bs-text);
  font-size: 1.5rem;
}

.submit-btn {
  width: 10rem;
  height: 3rem;
}

.submit-btn:hover, .submit-btn:active {
  background: linear-gradient(60deg, var(--bs-secondary) 10%, var(--bs-secondary-light) 60%, var(--bs-secondary));
  color: var(--bs-text);
}

.form-control::-webkit-input-placeholder {
  color: #181717;
}

#time {
  background-color: var(--bs-primary);
  border: 2px solid var(--bs-secondary);
  border-radius: 20px;
  width: fit-content;
  max-width: 100%;
  height: 7.5%;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-1rem);
  }
}

@keyframes dots {
  0%, 20% {
    content: ".";
  }

  40% {
    content: "..";
  }

  60% {
    content: "...";
  }

  80%, 100% {
    content: "";
  }
}

@media (width >= 769px) {
  .card .list-group-item {
    font-size: 25px;
  }
}
